<template>
    <div class="row vh-100">
        <div class="col-md-6 div-for-text">
            <div class="row">
                <div class="col-md-12">
                    <div class="row">
                        <div class="col-md-12">
                            <h3 class="text-center text-white">Contact us</h3>
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-6">
                            <input
                                type="text"
                                placeholder="First name"
                                class="form-control"
                                v-model="firstName"
                            />
                        </div>
                        <div class="col-md-6">
                            <input
                                type="text"
                                placeholder="Last name"
                                class="form-control"
                                v-model="lastName"
                            />
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <input
                                type="email"
                                placeholder="Email"
                                class="form-control"
                                v-model="email"
                            />
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <input
                                type="text"
                                placeholder="Phone"
                                class="form-control"
                                v-model="phone"
                            />
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <textarea
                                placeholder="Description"
                                class="form-control"
                                v-model="description"
                            ></textarea>
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <button class="bnt btn-block btn-secondary" @click="sendmail">Send</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-md-5 ml-auto pt-5 text-white div-for-text">
            <h1></h1>
            <p
                class="align-bottom"
            >If you have any questions or want to become our customer let me know by leaving a message</p>
        </div>
    </div>
</template>


<script>
import axios from "axios";
export default {
    name: "section-4",
    data: function() {
        return {
            firstName: "",
            lastName: "",
            email: "",
            phone: "",
            description: ""
        };
    },
    methods: {
        sendmail: function() {
            console.log("asdas", this.firstName);
            axios
                .post("http://www.xeniagaina.com/send-mail", {
                    firstName: this.firstName,
                    lastName: this.lastName,
                    email: this.email,
                    phone: this.phone,
                    description: this.description
                })
                .then(function(response) {
                    console.log(response.data);
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    }
};
</script>

<style scope>
.div-for-text {
    display: flex;
    align-items: center;
}
</style>